.address-header {
  width: 10.1rem;
  font-size: 0.8rem;
}

.address-column {
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 0.8rem;
  &:hover{
    overflow: visible;
  }
}

.token-headers {
  width: 4.4rem;
  text-align: center;
  white-space:normal;
  font-size: 0.8rem;
  vertical-align: middle !important;
}

.rank-headers {
  width: 2.5rem;
  text-align: center;
  white-space:normal;
  font-size: 0.8rem;
  vertical-align: middle !important;
}

.column {
    text-align: center;
    font-size: 0.8rem;
}

.expand {
  text-align: left;
  font-size: 0.8rem;
}
